.contact-me-container{
    width: 85%;
    margin:0 auto;
    height: max-content;
    padding: 2rem;
    display:flex;
    flex-direction:column;
    padding-top:10rem;
    padding-bottom: 10rem;
    .text-header{
      text-align: center;
      margin-bottom: 3rem;
      h2{
        line-height:1.2;
        letter-spacing: 1px;
        margin-bottom: 10px;
      }
    }
    .contact-sec{
display: flex;
height:100%;
width:100%;
.icon-section {
  height: 100%;
  width: 100%;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .social{
    width: 70%;
    height: 50px;
    display: flex;
    gap: 1em;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem;
    @media screen and(max-width:800px) {
      width: 100%;
    }
    i{
      padding: 1rem;
      border-radius: 10px;
      background-color: var(--primary-color);
      color: grey;
      cursor: pointer;
    }
  }
  .Call-me{
    display:flex;
    gap:1rem;
    width: 70%;
    margin-bottom: 3rem;
    background-color: beige;
    padding: 1rem 1.3rem;
    margin: 1 auto;
    @media screen and(max-width:800px) {
      width: 100%;
    }
    p{
      font-weight: 500;
      small{
        font-weight: 300;
      }
    }
  }
} 
} 
}