main{
    color:black;
    width: 90%;
    margin:0 auto;
   height: 100vh;
   background-position: center;
   background-repeat: no-repeat;
   background-size:cover;
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 1.5rem;
   @media screen and (max-width:800px){
            height: max-content;  
}
  .center{
      width:60%;
      text-align: center;
     
      button{
          @media screen and (max-width:800px){
              
          }
          a{
              color:white;
              font-weight: 400;
          }
      }
      h1{
        color:black;
        z-index: 5;
        font-size: 2.5rem;
        margin-bottom: 1rem;
      
      }
      p:last-of-type{
            font-size: 1rem;
          margin-bottom: 2rem;
     }
  } 
  .svg__img__container{
      position: relative;
      img{
          object-fit: cover;
          border-radius: 50%;
          width: 250px;
          height: 250px;
          position: absolute;
          top: 14%;
          left: 49%;
         transform: translateX(-49%);
      }
      @media screen and (max-width:800px) {
          
          display: flex;
          align-items: flex-end;
          padding-top: 5rem;
          img{
              top: 26%;
              left: 17%;
              
         transform: translateX(0);
          }
      }
  }
  svg{
    
      width: 400px;
      height: 400px;
      @media screen and (max-width:800px) {
          
      
    }
  }
  .icons{
      width: 10%;
      color:var(--primary-color);
      display:flex;
      gap:1rem;
      flex-direction: column;
    i{
        font-size:1.5rem;
    }
    }

    @media screen and (max-width:800px) {
        
    margin-bottom: 10rem;
     display: flex;
     flex-direction: column-reverse;   
     .icons{
         display:none;
    }
    .center{
        width:100%;
        
        h1{
            width:100%;
            font-size:2.2rem;
            margin-bottom: 1.5rem;
        }
        p:first-of-type{
   margin-top: -1rem;
   font-size: 2.1rem;
}
 p:last-of-type{
   z-index: 5;
   color:black;
     margin-bottom: 2rem;
}
    }
    }
   
    
}

main p:first-of-type{
   margin-top: -1rem;
   font-size: 1.6rem;
   margin-bottom: 1rem;
}
main p{
   z-index: 5;
   color:black;
   
}
