.footer__container {
  width: 90%;
  margin: 0 auto;
  background-color: var(--primary-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 70vh;
  padding: 2rem 0;
  .copyright {
    color: grey;
  }
  .inner-container {
    width: 70%;
    display: flex;
    justify-content: space-around;
    @media screen and(max-width:650px) {
      display: flex;
      flex-direction: column;
    }
    .socials {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
     
      i{
          color: white;
          font-size:1.7rem;
      }
      @media screen and(max-width:650px) {
        display:grid ;
        grid-template-columns: 1fr 1fr 1fr;
        margin-top: 1.5rem;
      }
    }
    .title {
      p {
        font-weight: bolder;
        margin-bottom: 1rem;
        font-size: 1.4rem;
        color: grey;
        small {
          font-weight: lighter;
          font-size: 1rem;
        }
      }
    }
    .link {
      ul {
        li {
          a {
            display: block;
            padding: 1rem 2rem;
            font-size: 1.3rem;
          }
        }
      }
    }
  }
}
