.skills-cont{
    width: 90%;
    height: 100vh;
    display:flex;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
     @media screen and (max-width:800px){
       padding-top:10rem;
   }
    h3{
        text-align:center;
        margin-bottom: 2rem;
        display: flex;
        flex-direction: column;
        color:grey;
    }
    small{
        text-align:center;
        color:grey;
    }
    .skills{
        width: 60%;
        display:flex;
        flex-wrap: wrap;
        gap:1.5rem;
        @media screen and (max-width:800px) {
           flex-direction: column;
            width: 100%;
            flex-wrap: nowrap;
        }
        
        .skill{
            background-color: beige;
            width:45%;
            cursor:pointer;
            padding: 1rem 1rem;
            @media screen and (max-width:800px) {
            
                width: 99%;
                margin: 0 auto;
            }
            .holder{
                display:flex;
                justify-content: space-between;
                align-items: center;
               .holder2{
                   display:flex;
                   i{
                       font-size:1.7rem;
                       margin-right: .7rem;
                   }
                   div{
                       h5{
                           font-weight: 400;
                            font-size :1\0.5rem;
                        @media screen and (max-width:800px){
                            font-size:1rem;
                        }
                       }
                       p{
                           font-size:0.7rem;
                           font-weight: 200;
                       }
                   }
               }
                i{
                    color: var(--primary-color);
                    cursor:pointer;
                }

                
            }
            
            .langu{
                width: 100%;
                height:max-content;
                padding:1.5rem;
                
                .php{
                    width: 100%;
                    span{
                        margin-top:.7rem;
                        width: 100%;
                        height:10px;
                        display:flex;
                        align-items:center;
                        border-radius: 10px;
                        background-color: var(--primary-color);
                        padding: .4rem 0rem;
                        justify-content: space-between;
                            p{

                           
                            height:10px;
                            border-radius: 10px;
                            background-color: blue;
                            }
                        
                    }
                }
            }
            }
    }
}

