.card-main-container{
    width:90%;
    margin: 0 auto;
    height: max-content;
    padding: 6rem 1rem;
    display:flex;
    align-items: center;
    justify-content: center;
 
}
.card-main-container .container-inner{
    width: 80%;
    height: 60%;
    background-color: var(--primary-color);
    padding: 2rem ;
    display:flex;
    border-radius: 30px;
    overflow: hidden;
}
.card-main-container .container-inner .text-section{
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap:1rem;
    color:rgb(255, 255, 255);
}
.text-section h3{
    margin-top: 2rem;
}
.text-section p{
    font-size: 20px;
    margin-bottom: 1.5rem;
}
.text-section button{
    display: flex;
    align-items: center;
    background-color: white;
    width: max-content;
    border-radius: 30px;
    padding:1.3rem 3rem;
    color:var(--primary-color);
    font-weight: bolder;
    margin-bottom: 1.5rem;
    border:solid 1px var(--primary-color);
    
}
.text-section button>i{
   font-size: 20px;
}
.container-inner .image-container{
    width:50%;
    position: relative; 
    background-color: rgb(255, 255, 255);
    
}
.container-inner .image-container img{
  width: 100%;
  height: 100%;
 object-fit: cover;
  
}


@media screen and (max-width:800px) {

    .container-inner .image-container{
     display:none;
 }
 .card-main-container {
    width: 100%;
 }
 .card-main-container .container-inner .text-section{
     width: 100%;
     height: 100%;
     display: flex;
     flex-direction: column;
    text-align: center;
    align-items: center;
    h3{
        font-size: 1rem;
        font-weight:bolder;
        text-transform: uppercase;
        line-height: 1.7;
    }
    p{
        font-size: 0.8rem;
        font-weight: 300;
    }
 } 
 }