.nav__container{
    width: 90vw;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
     left: 50%;
     transform: translateX(-50%);
     border: solid 1px rgba(128, 128, 128, 0.363);
    border-top: none;
    z-index: 10;
    background-color: whitesmoke;

    .logo h2 span{
        color: #ffe32e;
    }
    ul{
        display: flex;
        gap: 1rem;
      
    }
    ul li a{
        padding: 0.5rem;
        transition: all 1000ms ease;
        text-transform: capitalize;
     }
    
     @media screen and (max-width:720px) {
             .nav__container{
                display:none;
             }
         }
        
}

 .icon{
        display:none;
    }
@media screen and (max-width:800px){
    .icon{
        display:flex;
        margin-right: 3rem;
        cursor:pointer;
        color: var(--primary-color);
    }
    ul.links__ul{
        display:none;
    }
}
@media screen and (min-width:800px){
 
    .responsive__navBar{
        display:none;
    }
}

.responsive__navBar{
    top: 0vh;
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.993);
    .btnClose{
        float: right;
        font-weight: bolder;
        color:var(--primary-color);
        font-size: 1.5rem;
        margin-top: 2rem;
    }
    ul{
        margin-top: 8vh;
        width: 100%;
        height:100%;
        display:flex;
        flex-direction:column;
        align-items: flex-end;

        li{
            width: 100%;
            border-bottom: solid 1px var(--primary-color);
           padding: 1.5rem 0;
        }
    }
}


