@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,900;1,100;1,200;1,300;1,400;1,500;1,700&display=swap');

*, *::before, *::after{
    box-sizing: border-box;
    margin: 0;
    padding:0;
    list-style: none;
    text-decoration: none;
    outline: none;
    border: none;
}
a{
    color: var(--text-grey);
}
:root{
    --primary-color:#ffe32e;
    --text-grey:grey;
    --card-padding:1rem 2rem;
}
body{
    position:relative;
    overflow-x: hidden;
    width: 100vw;
      font-family: poppins,sans-serif;
}
.btn{
    padding: 1.3rem 4rem;
    border-radius: 15px;
    background: #ffe32e;
    color:white;
}
.btn.main{
    color:white;
     font-weight: 500;
     text-transform: uppercase;
 }