
.aboutme{
    width: 90%;
    height: 100vh;
    margin :0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    justify-content: center;
   @media screen and (max-width:800px){
       padding-top:10rem;
   }
}
.expo{
    display:flex;
    margin-top: 1rem;
    gap: 1.5rem;
}

