.image{
    justify-content:center;
    display:flex;
    align-items: center;
    background-color: lightgray;
    width: 80%;
    height: 80%;
    border-radius: 10%;
    img{
        width: 250px;
        aspect-ratio: 1/1;
        object-fit: cover;
    }
}
.aboutheader h3{
    margin-top: 2rem;
    color:gray;
    text-transform: uppercase;
}
.aboutme .desc{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 80%;
    place-items: center;
    
}
.desc svg{
    width: 80%;
    height: 80%;
}

.desc-main{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    height: 100%;
    p{
        font-size: 1.3rem;
        line-height: 2.3;
        @media screen and (max-width:700px) {
            font-size: 1.3rem;  
        }
    }
}
.desc-main button{
    margin-top: 1rem;
}

@media screen and(max-width:800px) {
    .aboutme .desc{
        display: flex;
        flex-direction: column;
        .image{
            display:none;
        }
    } 

 }