.services-container{
    width: 90%;
    margin: 0 auto;
    height: max-content;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 2rem;
     @media screen and(max-width:800px) {
            display: flex;
            align-items: center;
            height: max-content;
            margin: 0 auto;
            justify-content: center;
            width: 90%;
     }
      .service-title{
            margin: 4rem 0;
            text-align: center;
        }
    .service-card-container{
        display: flex;

        @media screen and(max-width:800px) {
                display: flex;
                width: 80%;
                flex-direction: column;
                gap: 2rem;
                justify-content: center;
                align-items: center;
                margin-top: 2rem;
               
                
            height:max-content;
        }
        .carddd{
            display:flex;
            height:max-content;
            @media screen and (max-width:800px) {
                display: flex;
                flex-direction: column;
                width: 100%;
                height:200vh;
                justify-content: center;
                align-items: center;
                gap: 2rem;
            }
            .card {
    
                width: 200px;
                height:250px;
                display: flex;
                gap: 1rem;
                background: #ffffff5b;
                flex-direction: column;
                justify-content: center;
                align-items: start;
                padding:1.5rem;
                border-radius: 20px;
                margin-right: 2.5rem;
                transition: all ease-out 0.5s;
                box-shadow: 5px 10px 10px 7px rgba(128,121,121,0.64)
                ;
                @media screen and(max-width:700px) {
                width: 100%;
                height: 300px;
                margin-right: 0;
            }
                i{
                    font-weight: bold;
                    font-size: 1.2rem;
                    color: var(--primary-color);
                }
                small{
                    display: flex;
                    align-items: center;
                }
            }
        }
        
      
    }
}




    .service-card-container .card:hover{
        // box-shadow: 5px 10px 10px -7px rgba(11, 197, 172, 0.64)
    
        transform: scale(0.9);
    }

 
    

